import Vue from 'vue'
import VueRouter from 'vue-router'
Vue.use(VueRouter)
const routes = [
  {
    path: '/',
    name: 'login',
    component: () => import('../views/LoginView.vue')
  },
  {
    path: '/home',
    name: 'home',
    component: () => import('../views/HomeView.vue'),
    redirect:"/homeinfo",
    children: [
      {
        path: "/charging",
        name: "charging",
        component: () => import("../views/ChargingView.vue")
      },
      {
        path: "/number",
        name: "number",
        component: () => import("../views/NumbermanagementView.vue")
      },
      {
        path: "/authority",
        name: "authority",
        component: () => import('../views/AuthorityView.vue')
      },
      {
        path: '/business',
        name: 'business',
        component: () => import('../views/BusinessView.vue'),
      },
      {
        path: "/homeinfo",
        name: 'homeinfo',
        component: () => import('../views/HomeDataView.vue'),
        //路由重定向
        redirect:"/enterprisedata",
        children: [
          {
            // 子路由 企业数据
            path: '/enterprisedata',
            name: 'enterprisedata',
            component: () => import('../views/EntDataView.vue')
          },
          {
            path: '/Serverdata',
            name: 'Serverdata',
            component: () => import('../views/ServerDataView.vue')
          },
          {
            path: '/Provdata',
            name: 'Provdata',
            component: () => import('../views/ProvdataView.vue')
          }
        ]
      }
    ]
  },
]

const router = new VueRouter({
  routes
})

router.beforeEach((to, from, next) => {
  // 在导航之前进行检查
  const isAuthenticated = checkCookie(); // 调用检查cookie的函数
  if (to.name !== 'login' && !isAuthenticated) {
    alert('请先登录');
    next({ name: 'login' });
  } else {
    next(); // 确保一定要调用 next()
  }
});

function checkCookie() {
  // 从document.cookie中获取所有cookie
  const cookies = document.cookie.split(';');
  // 遍历所有cookie，查找特定的cookie值
  for (let i = 0; i < cookies.length; i++) {
    const cookie = cookies[i].trim();
    // 检查是否包含名为token的cookie
    if (cookie.startsWith('token=')) {
      // 如果找到了名为token的cookie，表示用户已登录
      return true;
    }
  }
  // 如果未找到名为token的cookie，表示用户未登录
  return false;
}

export default router